export function fordFulkerson(source: string, sink: string, arcs: { [id: string]: { [id: string]: number } }) {

  const flow: { [id: string]: { [id: string]: number } } = {}

  const backwardArcs = Object.keys(arcs).reduce<{ [from: string]: Set<string> }>((acc, to) => {
    for (const from of Object.keys(arcs[to])) {
      if(!acc[from]){
        acc[from] = new Set()
      }
      acc[from].add(to)
    }
    return acc
  }, {})

  function findPath(node: string, visited: string[], increase: number): { visited: string[], increase: number } | null {
    const newVisited = [...visited, node]

    if (node === sink) {
      //we have arrived
      return {
        visited: newVisited,
        increase
      }
    }

    let path = null
    //forward arcs
    for (const [dest, capacity] of Object.entries(arcs[node] ?? {})) {

      if (visited.find((e) => e === dest)) {
        continue
      }

      const currentFlow = flow[node]?.[dest] ?? 0

      const arcIncrease = capacity - currentFlow

      if (arcIncrease > 0) {
        path = findPath(dest, newVisited, Math.min(increase, arcIncrease))
        //we got a path
        if (path) {
          break
        }
      }
    }

    if (!path) {
      //look in backward arcs too
      for (const dest of [...(backwardArcs[node]??[])]) {
        if (visited.find((e) => e === dest)) {
          continue
        }
        const currentFlow = flow[node]?.[dest] ?? 0
        const arcIncrease = currentFlow
        if (arcIncrease > 0) {
          path = findPath(dest, newVisited, Math.min(increase, arcIncrease))
          //we got a path
          if (path) {
            break
          }
        }
      }
    }

    return path
  }

  let augmentingPath = findPath(source, [], Infinity)
  while (augmentingPath) {
    const visited = augmentingPath?.visited
    for (let i = 0; i < visited.length - 1; i++) {
      const [a, b] = [visited[i], visited[i + 1]]
      if (!flow[a]) {
        flow[a] = {}
      }
      //check if it was a backward arc
      const arcIncrease = augmentingPath.increase * (arcs[a][b] ? 1 : -1)

      if (!flow[a][b]) {
        flow[a][b] = arcIncrease
      } else {
        flow[a][b] += arcIncrease
      }
    }

    augmentingPath = findPath(source, [], Infinity)
  }


  return flow
}